import {
  iosVhFix
} from './utils/ios-vh-fix';
import {
  initModals
} from './modules/modals/init-modals';
import {
  Form
} from './modules/form-validate/form';
import {
  showLogo
} from './modules/show-logo';
import {
  showMenu
} from './modules/show-menu';
import {
  testWebP
} from './utils/test-webp';
import {
  setFocusCard
} from './modules/set-focus-card';
import {
  showCards
} from './modules/show-cards';
import {
  initMap
} from './modules/init-map';
import {
  setMaskTel
} from './modules/set-mask-tel';
import {
  initLoadImages
} from './modules/init-load-images.js';
// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  testWebP();

  // ---------------------------------
  iosVhFix();
  // Modules

  initLoadImages();
  showLogo();
  showMenu();
  // ---------------------------------
  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    initModals();
    const form = new Form();
    window.form = form;
    form.init();
    setFocusCard();
    showCards();
    initMap();
    setMaskTel();
  });
});

